import * as React from "react";
import { Routes, Route, Outlet, Link } from "react-router-dom";
import "./MainpageStyles.css";

import OldTodo from "./todo/todo";
import Timer from "./timer/timer";
import Menu from "./menu/menu";
import Keyboard from "./keyboard/keyboard";
import Album from "./album/album";
import Slider from "./slider/slider";
import Calculator from "./calculator/calculator";
import Weather from "./weather/weather";
import Validate from "./validate/validate";
import Rps from "./rps/rps";
import Calendar from "./calendar/calendar";
import Youtube from "./youtube/youtube";
import ColorGen from "./colorGen/colorGen";
import PassGen from "./passGen/passGen";
import LoremGen from "./loremGen/loremGen";
import Quiz from "./quiz/quiz";
// https://store.selfteach.me/

export default function App() {
  return (
    <>
      <Routes>
        <Route path="/daily-challenges/" element={<Layout />}>
          <Route index element={<MyProjects />} />
          <Route path="/daily-challenges/oldtodo" element={<OldTodo />} />
          <Route path="/daily-challenges/timer" element={<Timer />} />
          <Route path="/daily-challenges/menu" element={<Menu />} />
          <Route path="/daily-challenges/keyboard" element={<Keyboard />} />
          <Route path="/daily-challenges/album" element={<Album />} />
          <Route path="/daily-challenges/slider" element={<Slider />} />
          <Route path="/daily-challenges/calculator" element={<Calculator />} />
          <Route path="/daily-challenges/weather" element={<Weather />} />
          <Route path="/daily-challenges/validate" element={<Validate />} />
          <Route path="/daily-challenges/rps" element={<Rps />} />
          <Route path="/daily-challenges/calendar" element={<Calendar />} />
          <Route path="/daily-challenges/youtube" element={<Youtube />} />
          <Route path="/daily-challenges/colorGen" element={<ColorGen />} />
          <Route path="/daily-challenges/passGen" element={<PassGen />} />
          <Route path="/daily-challenges/loremGen" element={<LoremGen />} />
          <Route path="/daily-challenges/quiz" element={<Quiz />} />
          <Route path="/daily-challenges/*" element={<MyProjects />} />
        </Route>
      </Routes>
    </>
  );
}

function Layout() {
  return (
    <>
      <Link to="/daily-challenges/" className='linktomainpage'>
        <img src="arrow_back.svg" alt="" />
      </Link>
      <div className="page">
        <Outlet />
      </div>
    </>
  );
}

